.App {
  display: grid;
  grid-template-rows: auto 1fr;
  grid-template-columns: 0 1fr 0;
}

.App-header {
  grid-row-start: 1;
  grid-column-start: 2;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  margin-left: 20px;
  margin-right: 20px;
  border-bottom-width: 1px;
  --border-opacity: 1;
  border-color: #cbd5e0;
  border-color: rgba(203, 213, 224, var(--border-opacity));
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.App-header-title {
  font-size: 1.25rem;
  font-weight: 700;
}

.App-header-button-container {
  flex-grow: 0;
}

.App-header-button {
  margin-left: 0.25rem;
  border-radius: 0.25rem;
  width: 2rem;
  height: 2rem;
  --bg-opacity: 1;
  background-color: #b2f5ea;
  background-color: rgba(178, 245, 234, var(--bg-opacity));
}

.App-main {
  grid-row-start: 2;
  grid-column-start: 2;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 20px;
  gap: 20px;
  place-items: start;
  margin: 20px;
}

.App-main-add {
  position: relative;
  margin-top: 2.5rem;
  border-width: 4px;
  --border-opacity: 1;
  border-color: #4299e1;
  border-color: rgba(66, 153, 225, var(--border-opacity));
  border-style: dashed;
  width: 100%;
  --bg-opacity: 1;
  background-color: #90cdf4;
  background-color: rgba(144, 205, 244, var(--bg-opacity));
  --bg-opacity: 0.25;
}

.App-main-add-ratio {
  padding-bottom: 56.25%;
}

.App-main-add-button {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
}

.App-footer {
  grid-row-start: 3;
  grid-column-start: 2;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 20px;
  border-top-width: 1px;
  --border-opacity: 1;
  border-color: #cbd5e0;
  border-color: rgba(203, 213, 224, var(--border-opacity));
  padding-left: 0.25rem;
  padding-right: 0.25rem;
  padding-top: 0.75rem;
}

.App-footer-title {
  margin-bottom: 0.25rem;
  font-size: 1.125rem;
  font-weight: 700;
}

.App-footer-config-list {
  display: flex;
  border-bottom-width: 1px;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  max-width: 360px;
}

.App-footer-config-list:last-child {
  border-style: none;
}

.App-footer-config-key {
  flex: 1 1 0%;
}

.App-footer-config-value {
  flex-grow: 0;
}

@media (min-width: 400px) {
  .App {
    grid-template-columns: 1fr 400px 1fr;
  }

  .App-main {
    display: grid;
    grid-template-columns: 360px;
  }
}

@media (min-width: 780px) {
  .App {
    grid-template-columns: 1fr 780px 1fr;
  }

  .App-main {
    display: grid;
    grid-template-columns: repeat(2, 360px);
  }
}

@media (min-width: 1160px) {
  .App {
    grid-template-columns: 1fr 1160px 1fr;
  }

  .App-main {
    display: grid;
    grid-template-columns: repeat(3, 360px);
  }
}

@media (min-width: 1540px) {
  .App {
    grid-template-columns: 1fr 1540px 1fr;
  }

  .App-main {
    display: grid;
    grid-template-columns: repeat(4, 360px);
  }
}
