.ScreenForm-function {
  margin-left: 0.25rem;
  margin-bottom: 0.5rem;
}

.ScreenForm-function-button {
  margin-right: 0.25rem;
  border-radius: 0.25rem;
  width: 2rem;
  height: 2rem;
  --bg-opacity: 1;
  background-color: #cbd5e0;
  background-color: rgba(203, 213, 224, var(--bg-opacity));
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.ScreenForm-function-button-negative {
  margin-right: 0.25rem;
  border-radius: 0.25rem;
  width: 2rem;
  height: 2rem;
  --bg-opacity: 1;
  background-color: #cbd5e0;
  background-color: rgba(203, 213, 224, var(--bg-opacity));
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --bg-opacity: 1;
  background-color: #feb2b2;
  background-color: rgba(254, 178, 178, var(--bg-opacity));
}

.ScreenForm-screen {
  position: relative;
  width: 100%;
  line-height: 1.25;
}

.ScreenForm-ratio {
  padding-bottom: 56.25%; /* Default padding-bottom */
}

.ScreenForm-content {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  --bg-opacity: 1;
  background-color: #e2e8f0;
  background-color: rgba(226, 232, 240, var(--bg-opacity));
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.ScreenForm-grid {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: grid;
  grid-template-rows: repeat(3, minmax(0, 1fr));
  grid-template-columns: repeat(3, minmax(0, 1fr));
  place-content: stretch;
  place-items: stretch;
  height: 100%;
}

.ScreenForm-grid-item {
  place-self: center;
  text-align: center;
}

.ScreenForm-width {
  grid-row-start: 3;
  grid-row-end: 4;
  grid-column-start: 2;
  grid-column-end: 3;
}

.ScreenForm-height {
  grid-row-start: 2;
  grid-row-end: 4;
  grid-column-start: 3;
  grid-column-end: 4;
}

.ScreenForm-diagonal {
  grid-row-start: 2;
  grid-row-end: 3;
  grid-column-start: 2;
  grid-column-end: 3;
}

.ScreenForm-grid-item > li {
  font-size: 0.75rem;
  letter-spacing: -0.025em;
}

.ScreenForm-input {
  border-width: 1px;
  --border-opacity: 1;
  border-color: #718096;
  border-color: rgba(113, 128, 150, var(--border-opacity));
  border-radius: 0.25rem;
  width: 3.5rem;
  line-height: 1.375;
  font-size: 1rem;
  text-align: center;
}
