.ScreenFormMisc {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  bottom: auto;
  padding: 0.25rem;
  font-size: 0.875rem;
}

.ScreenFormMisc-pairs span {
  display: inline-block;
}

.ScreenFormMisc-pair {
  margin-bottom: 1px;
}

.ScreenFormMisc-key {
  border-radius: 0.25rem;
  margin-top: 1px;
  margin-left: 1px;
  padding-left: 0.25rem;
  --bg-opacity: 1;
  background-color: #81e6d9;
  background-color: rgba(129, 230, 217, var(--bg-opacity));
  --text-opacity: 1;
  color: #234e52;
  color: rgba(35, 78, 82, var(--text-opacity));
}

.ScreenFormMisc-value {
  margin-left: 0.25rem;
  margin-right: 0.75rem;
}
